footer[id=site-specifics] {
	margin: 0 auto;
	position: relative;
	clear: left;
	z-index: 10;
	overflow: hidden;
	background: $color-accent;
	padding: 40px 15px 30px;
	color: #fff;
	@include before-desktop {
		text-align: center;
	}
	@include desktop {
		margin: 0 auto;
		text-align: right;
		font-size: 18px;
		line-height: 18px;
	}
	ul {
		max-width: 1240px;
		margin: 0 auto;
		// padding: 0 0 1.875rem 1.875rem;
		position: relative;
		@include desktop {
			font-size: 12px;
			line-height: 18px;
		}
		&:before {
			content: '';
			position: absolute;
			width: 99px;
			height: 36px;
			top: 0;
			left: 1.875rem;
			background-image: url(../../img/logo/novartis-hcp-portal@2x.png);
			background-repeat: no-repeat;
			background-size: contain;
		}
	}
	li {
		display: inline-block;
		padding: 5px 15px;
		@include desktop {
			padding-top: 0;
			padding-bottom: 0;
			min-height: 20px;
			border-left: solid 1px #363836;
			&:first-child,
			&:nth-child(2) {
				border-left: 0;
			}
		}
		a {
			text-decoration: none;
			color: #fff;
			@include desktop {
				position: relative;
				top: -1px;
			}
		}
	}
	p {
		@include desktop {
			margin: 0;
			padding: 0;
			font-size: 18px;
			line-height: 1;
		}
	}
