	.copy-novartis-global {
		padding: 5px 0;
		@include before-desktop {
			text-align: center;
			display: block;
			color: #fff;
		}
		@include desktop {
			// position: relative;
			// top: -1px;
			// padding-top: 0;
			// text-align: right;
			// width: 180px;
			// border-left: 0;
			// float: right;
			// color: $color-accent;
			display: block;
			width:100%;
			text-align:center;
			color:#fff;
		}
		img {
			height: auto;
			margin-right: 140px;
			max-width: 180px;
		}
		span {
			font-size: 26px;
			line-height: 18px;
			position: relative;
			top: 3px;
		}

		div a{
			color:#0061ab !important;
		}
	}
	.no-touch & {
		a {
			@include prefixed(transition, all .3s ease-in-out);
			li & {
				&:hover {
					color: $color-accent;
				}
			}
		}
	}
